@import "../../scss/variables";
@import "../../scss/mixins";

.VendorPerformanceTop {
  table {
    border-collapse: separate;
    @include mixer(72);
    &:has(.parentCadre) {
      tr {
        th,
        td {
          &:nth-of-type(3),
          &:nth-of-type(4) {
            min-width: 100px;
            max-width: 100px;
            position: sticky;
          }
          &:nth-of-type(3) {
            left: 340px;
          }
          &:nth-of-type(4) {
            left: 440px;
          }
        }
      }
    }
  }
  .MuiTableContainer-root {
    max-height: 40vh;
    overflow-y: auto;
  }
  thead {
    position: sticky;
    top: 0;
    z-index: 3;
    tr {
      th:has(.mixer) {
        span:nth-of-type(1) {
          max-width: 135px;
          display: inline-table;
        }
      }
      th {
        &:nth-of-type(2) {
          max-width: 210px !important;
          min-width: 210px !important;
          .MuiTableSortLabel-root {
            display: none !important;
          }
        }
        vertical-align: middle;
      }

      &:nth-of-type(2) {
        th {
          & > div {
            width: 100%;
            display: flex;
            justify-content: center;
          }
        }
      }
    }
  }

  tbody {
    td {
      text-align: right;
      &:nth-of-type(2) {
        text-align: left;
      }
    }

    :has(.childCadre) {
      background: $white !important;
      text-align: center;
    }

    :has(.childRow) {
      background: $white !important;
    }

    :has(.parentRow) {
      background: #d4e7fb !important;
    }

    :has(.parentCadre) {
      background: #d4e7fb !important;
    }
  }

  tr {
    th:nth-of-type(1) {
      position: sticky;
      left: 0;
      z-index: 3 !important;
    }
    th:nth-of-type(2) {
      position: sticky !important;
      left: 60px;
      z-index: 3;
    }
    td:nth-of-type(1) {
      position: sticky;
      left: 0;
      z-index: 1;
    }
    td:nth-of-type(2) {
      position: sticky;
      left: 60px;
      z-index: 1;
    }
  }
}

.vendorResourceTable {
  .p-datatable.p-datatable-gridlines .p-datatable-thead > tr > th {
    border-width: 0 1px 0 0 !important ;
  }
  .p-datatable-wrapper {
    // max-height: 40vh;
    overflow-y: auto;
    table {
      border-collapse: separate;
      border-spacing: 0;
      thead {
        position: sticky;
        top: 0;
        z-index: 2;
      }
      tr {
        th,
        td {
          border-left: 0 !important;
          &:nth-of-type(1) {
            position: sticky;
            left: 0;
            min-width: 120px;
            max-width: 120px;
          }
          &:nth-of-type(2) {
            position: sticky;
            left: 120px;
          }
        }
      }
    }
  }
}
