@import "../../scss/variables";

.cslPartnerTable {
  table {
    thead {
      tr {
        th {
          div {
            justify-content: center;
          }
        }
      }
    }
    tbody {
      tr {
        &:has(.cslPartner) td {
          background-color: #d4e7fb !important;
        }
      }
    }
  }
}

