.roleProject {
  padding: 0;
  width: 100%;
  max-width: 25%;
  table {
    width: 100%;
    border-collapse: collapse;
    tbody {
      tr {
        td {
          max-width: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          padding: 5px; /* Add padding to improve appearance */
        }
      }
    }
  }
}
