@import "../../scss/variables";
@import "../../scss/mixins";

.billableUtilTrendTable {
  font-size: $tableFont;

  table {
    border-collapse: separate;
    @include mixer(49);
    tr {
      th,
      td {
        &:nth-of-type(1) {
          position: sticky;
          left: 0;
          min-width: 200px;
          max-width: 200px;
        }
        &:has(.cader) {
          min-width: 80px;
          max-width: 80px;
          div {
            width: 100%;
          }
        }
      }
    }

    thead {
      opacity: 1;
      tr {
        &:first-of-type {
          th {
            line-height: 1.2;
            vertical-align: middle;
            &:first-of-type {
              z-index: 2;
            }
          }
        }
      }
    }
    tbody {
      tr {
        td {
          &:nth-last-of-type(1),
          &:nth-last-of-type(2),
          &:nth-last-of-type(3),
          &:nth-last-of-type(4),
          &:nth-last-of-type(5) {
            div {
              width: 100%;
              text-align: right;
            }
          }
          div {
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
          }
          &:has(.doj) {
            div {
              width: 100%;
            }
          }
        }
      }
    }
  }
}
.billableUtilTrendTable.darkHeader:not(.toHead) tbody tr:first-of-type {
  td {
    background-color: $tableSummaryGray !important;
  }
}
