@import "../../scss/variables";
@import "../../scss/mixins";

.VendorPerformance {
  font-size: 13px;

  & > div {
    box-shadow: 0 0 0;
    border-left-color: $lightGray;
  }
  table {
    tr {
      td {
        &:has(.odd) {
          background-color: #f5e5e5;
          justify-content: flex-end;
        }

        &:has(.even) {
          background-color: #d8eaea;
          justify-content: flex-end;
        }

        &:has(.total) {
          background-color: #ffffe6;
          justify-content: flex-end;
        }

        &:has(.name) {
          background-color: #d4e7fb;
          // justify-content: flex-end;
        }

        // &:has(.sm) {
        //     min-width: 50px;
        //     max-width: 50px;
        //     width: 50px;
        // }
      }
    }
  }
}

.resourceAllocationTable {
  & > .darkHeader {
    @include mixer(49);
  }
}
