@import "../../scss/variables";

.projectHealthTable {
  table.p-datatable-table {
    font-size: $tableFont !important;
    th,
    td {
      padding: 1px 2px !important;
      min-width: 100px;
    }
    th {
      font-size: 12px !important;
      &:has([stroke="currentColor"]) {
        .p-column-title {
          display: flex;
          align-items: center;
          gap: 5px;
          div {
            display: inline-table;
          }
        }
      }
      .p-sortable-column-icon {
        width: auto !important;
      }
    }

    thead {
      tr {
        th {
          & > div {
            justify-content: center;
          }
        }
        &:nth-of-type(1) {
          th {
            &:first-of-type {
              min-width: 350px;
              max-width: 350px;
            }
          }
        }
        &:nth-of-type(2) {
          min-width: 200px;
          max-width: 200px;
        }
        &:nth-of-type(3) {
          min-width: 150px;
          max-width: 150px;
        }
      }
    }
    tbody {
      tr {
        td {
          &:first-of-type {
            min-width: 350px;
            max-width: 350px;
            .legendContainer {
              .legend {
                min-width: 100%;
                max-width: 100%;
                .legendTxt {
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  width: calc(100% - 20px);
                }
              }
            }
          }
          &:nth-of-type(2) {
            min-width: 200px;
            max-width: 200px;
          }
          &:nth-of-type(3) {
            min-width: 150px;
            max-width: 150px;
          }
        }
      }
    }
    .highlighted-row {
      background-color: #fff4fa !important; /* Change this to the highlight color you prefer */
      /* Add other styles as needed */
    }
    .nonHighlighted-row {
      background-color: white !important; /* Change this to the highlight color you prefer */
      /* Add other styles as needed */
    }
    .complete {
      background-color: #f2f9f1 !important;
    }
    .gmRows {
      background-color: #f2feff !important;
    }
    .riskRows {
      background-color: #f4f8ff !important;
    }
  }
}
